body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --rgb-white: 255, 255, 255;
  --color-white: rgb(var(--rgb-white));
  --rgb-grey: 238, 238, 242;
  --color-grey: rgb(var(--rgb-grey));
  --rgb-gray: var(--rgb-grey);
  --color-gray: var(--color-grey);
  --rgb-blue: 69, 73, 234;
  --color-blue: rgb(var(--rgb-blue));
  --rgb-black: 77, 77, 77;
  --color-black: rgb(var(--rgb-black));
  --rgb-pink: 255, 107, 192;
  --color-pink: rgb(var(--rgb-pink));
  --rgb-purple: 115, 120, 255;
  --color-purple: rgb(var(--rgb-purple));
  --rgb-cyan: 93, 187, 255;
  --color-cyan: rgb(var(--rgb-cyan));
  --rgb-green: 50, 228, 3;
  --color-green: rgb(var(--rgb-green));
  --rgb-yellow: 249, 231, 0;
  --color-yellow: rgb(var(--rgb-yellow));
  --rgb-salmon: 255, 100, 107;
  --color-salmon: rgb(var(--rgb-salmon));
  --rgb-red: 243, 48, 59;
  --color-red: rgb(var(--rgb-red));
}

body {
  background-color: var(--color-grey);
  color: var(--color-black);
}
