.textArea_wrapper {
  background-color: var(--color-white);
  border-radius: 1em;
  padding: 1em;
}
.text-area_label {
  display: block;
  padding: 0 0.5em;
}
.text-area_area {
  border: 2px solid var(--color-gray);
  box-sizing: border-box;
  min-height: 5em;
  padding: 0.5em;
  resize: vertical;
  width: 100%;
}
